

























import * as Sentry from "@sentry/browser";

import Vue from "vue";
export default Vue.extend({
	mounted() {
		this.$nextTick(function() {
			this.$store.commit("setJWTToken", undefined);
			Sentry.configureScope((scope) => {
				scope.setUser({});
			});
			this.$router.replace("/");
		});
	},
});
